.App {
  text-align: center;
  box-sizing: border-box;
  height: 100%;
  margin: 0 auto;
  font-family: 'Nunito Sans', sans-serif;
}

a { 
  text-decoration: none;
}

a:link, a:visited {
  color: black
}

main {
  display: flex !important; 
  flex-direction: row;
  width: 100%;
  justify-content: flex-start;
}

.menu {
  /* order: 1; */
  display: block;
  border: black 1px solid;
  padding: 20px;
}

.main-content {
  flex: 4 1;
  margin: 10px auto;
  padding: 0 5%;
}

.title {
  font-size: 2rem;
  padding: 5px;
}

form {
  display: flex;
  max-width: 500px;
  margin: auto;
  padding: 10px;
  flex-direction: column;
  width: 80%;
}

.task_form {
  width: 100%
}

input {
  display: block;
  margin: 7px auto !important;
  padding: 5px;
  width: 90%;
}

select {
  margin-left: 5px !important; 
}

.back_button {
  border-radius: 5px;
  padding: 10px;
  margin: 7px auto;
  background-color: #008b00;
  color: white;
}

.back_button:hover {
  background-color: #01aa01;
}

.page_header {
  font-size: 2rem;
  font-weight: 800;
  padding: 10px 0;
}

.sub_header {
  font-size: 1.5rem;
  font-weight: 600;
  padding: 10px 0;
}

.edit_button {
  padding: 10px;
  margin: 0 10px; 
  border-radius: 5px;
  background-color: #008b00;
  color: white;
}


.edit_button:hover {
  background-color: #01aa01;
}

.add_button {
  padding: 10px;
  border-radius: 5px;
  margin: 0 10px; 
  background-color: #008b00;
  color: white;
}

.add_button:hover {
  background-color: #01aa01;
}

input, select {
  border: solid 1px #a7a5a5;
}

@media screen and (max-width: 600px) {
  main {
    flex-direction: column;
  }

  .main-content {
    padding: 0 5%;
  }
}
.ProjectList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    text-align: center
}

.ProjectListItem {
    border: solid 1px black;
    border-radius: 5px;
    padding: 1em;
    margin: auto;
    height: 100px;
    width: 100px;
    display: flex;
    margin: 10px auto;
    -webkit-transition-duration: .4s;
            transition-duration: .4s;
}

.ProjectListItem:hover {
    background: #6deb73;
    -webkit-transform: translate3d(0, -5px, 0);
            transform: translate3d(0, -5px, 0);
}

.project_title {
    margin: auto;
}
.TaskListItem {
    margin: 5px 0;
    padding: 7px;
    border: 1px solid #a5a4a4;
    border-radius: 5px;
    text-align: left;
}

.TaskListItem:hover {
    background-color: #b1acac;
}
.TaskList {
    margin: auto;
    padding: 10px 0;
}
.member_item {
    padding: 2px;
    color: #555555;
}
.page_description {
    text-align: left;
    padding-bottom: 1rem; 
}
@media screen and (max-width: 600px) {
    .side-menu {
      display: none;
    }
}

